.page-main__image-wrapper {
  background-color: $background-color-second;
  min-height: 159px;
}

@media (min-width: $tablet-width) {
  .page-main__image-wrapper {
    min-height: 291px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__image-wrapper {
    min-height: 305px;
  }
}

.page-main__image-wrapper.page-main__image-wrapper--index {
  min-height: 389px;
}

@media (min-width: $tablet-width) {
  .page-main__image-wrapper.page-main__image-wrapper--index {
    min-height: 518px;
  }
}

.page-main__text-unit {
  padding: 35px 20px 30px;
  border-bottom: 1px solid $border-color;
}

.unit-wrap {
  max-width: 450px;
  margin: 0 auto;

  &--form {
    max-width: 440px;
    margin: 0 auto;
  }

  &--catalog {
    max-width: 440px;
    margin: 0 auto;
  }
}


@media (min-width: $tablet-width) {
  .unit-wrap {
    max-width: 490px;
    margin: 0 auto;
  }

  .page-main__text-unit {
    border-bottom: none;
  }
}

.page-main__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  color: $font-color-title;
  padding: 0 10px 44px;
  margin: 0;
}

@media (min-width: $tablet-width) {
  .page-main__title {
    font-size: 21px;
  }

  .page-main__title--form {
    font-size: 30px;
  }

  .page-main__title--catalog {
    font-size: 30px;
  }
}

.page-main__text {
  font-size: 14px;
  line-height: 26px;
  text-align: center;
  font-weight: 400;
  color: $font-color-base;
  margin: 0;
}
